	module.exports = {"data":{"post":{"html":"<p>The Shopblogger Björn Harste is <a href=\"http://www.shopblogger.de/blog/archives/5203-24-Stunden-geoeffnet.html\">taking advantage</a> of the extended shopping hours in Bremen and is now opening his Spar supermarket 24 hours from Monday through Saturday. He <a href=\"http://www.shopblogger.de/blog/archives/5205-14-Kunden.html\">blogged</a> <a href=\"http://www.shopblogger.de/blog/archives/5208-gaehn.html\">through</a> the <a href=\"http://www.shopblogger.de/blog/archives/5214-Bilanz-der-ersten-Nachtoeffnung.html\">night</a> his \"first night out\".</p>\n<p>Of course for restaurants 24 hour opening is nothing new or special. We are already open until 3 am (4 am on weekends). We're considering going 24 hours ourselves (at least in the Drive-Thru), not so much for the sales as for daily maintenance and security. There's always something to clean.</p>","fields":{"title":"24/7","date":"18 Jun 2007","url":"https://papascott.de/2007-06-18-247/"}}},"pathContext":{"slug":"/2007-06-18-247/","prev":{"fields":{"title":"Fun Fun Fun","date":"17 Jun 2007","slug":"/2007-06-17-fun-fun-fun/"}},"next":{"fields":{"title":"Censr at Flickr","date":"20 Jun 2007","slug":"/2007-06-20-censr-at-flickr/"}}}}

